import { registerBlockType } from '@wordpress/blocks';
import ServerSideRender from '@wordpress/server-side-render';

import {
	InspectorControls,
	useBlockProps,
} from '@wordpress/block-editor';

import {
	TextControl,
	ToggleControl,
	SelectControl,
	PanelBody,
} from '@wordpress/components';

const { __ } = wp.i18n;
registerBlockType('filter-plus/wp-filter', {
    title: __( 'Wordpress Filter', 'filter-plus' ),
    icon: 'image-filter',
    category: 'text',
	attributes: {
		filter_type: {
			type: 'string',
			default: 'post'
		},
		custom_post: {
			type: 'string',
		},
		template: {
			type: 'string',
			default: '1'
		},
		show_categories: {
			type: 'string',
		},
		category_label: {
			type: 'string',
		},
		categories: {
			type: 'array',
			default: [] ,
		},
		show_tags: {
			type: 'boolean',
		},
		tag_label: {
			type: 'string',
		},
		tags: {
			type: 'array',
			default: [] ,
		},
		author: {
			type: 'boolean',
		},
		author_label: {
			type: 'string',
		},
		author_list: {
			type: 'array',
			default: [] ,
		},
		custom_field: {
			type: 'boolean',
		},
		custom_field_label: {
			type: 'string',
		},
		meta_condition: {
			type: 'string',
			default: 'OR'
		},
		custom_field_list: {
			type: 'string',
		},
		post_tags: {
			type: 'boolean',
		},
		post_categories: {
			type: 'boolean',
		},
		post_author: {
			type: 'boolean',
		}
	},

	edit( { attributes, setAttributes } ) {
        const blockProps = useBlockProps();

        const {
			filter_type,
			custom_post,
			template,
			show_categories,
			category_label,
			categories,
            show_tags,
			tag_label,
            tags,
            author,
            author_label,
            author_list,
            custom_field,
            custom_field_label,
            meta_condition,
            custom_field_list,
			post_categories,
			post_tags,
			post_author
        } = attributes;
		function onChangeFilterType( newValue ) {
            setAttributes( { filter_type: newValue } );
        }
		function onChangeCustomPost( newValue ) {
            setAttributes( { custom_post: newValue } );
        }
		function onChangeTemplate( newValue ) {
            setAttributes( { template: newValue } );
        }
		function onChangeShowCategory( newValue ) {
            setAttributes( { show_categories: newValue } );
        }
		function onChangeCategoryLabel( newValue ) {
            setAttributes( { category_label: newValue } );
        }
		function onChangeCatList( newValue ) {
            setAttributes( { categories: newValue } );
        }
		function onChangeDisplayTags( newValue ) {
            setAttributes( { show_tags: newValue } );
        }
		function onChangeTagLabel( newValue ) {
            setAttributes( { tag_label: newValue } );
        }
		function onChangeTagist( newValue ) {
            setAttributes( { tags: newValue } );
        }
        function onChangeShowAuthor( newValue ) {
            setAttributes( { author: newValue } );
        }
        function onChangeAuthorLabel( newValue ) {
            setAttributes( { author_label: newValue } );
        }
		function onChangeAuthorList( newValue ) {
            setAttributes( { author_list: newValue } );
        }
		function onChangeCustomField( newValue ) {
            setAttributes( { custom_field: newValue } );
        }
		function onChangeCustomFieldLabel( newValue ) {
            setAttributes( { custom_field_label: newValue } );
        }
		function onChangeMetaCondition( newValue ) {
            setAttributes( { meta_condition: newValue } );
        }
		function onChangeCustomFieldList( newValue ) {
            setAttributes( { custom_field_list: newValue } );
        }
		function onChangePostCat( newValue ) {
            setAttributes( { post_categories: newValue } );
        }
		function onChangePostTag( newValue ) {
            setAttributes( { post_tags: newValue } );
        }
		function onChangePostAuthor( newValue ) {
            setAttributes( { post_author: newValue } );
        }
		function pro_text() {
			return filterPlus.is_pro_active == 1 ? __( '(Pro)', 'filter-plus' ) : '';
		}
		function is_pro_active() {
			return filterPlus.is_pro_active == 0 ? 0 : 1;
		}
		function getTemplates() {
			let templates = [ { value: 1 , label: __( 'Template-1', 'filter-plus' )+' '+pro_text() , disabled: is_pro_active } ];
			templates.push({ value: 2 , label: __( 'Template-2', 'filter-plus' )+' '+pro_text() , disabled: is_pro_active });
			templates.push({ value: 3 , label: __( 'Template-3', 'filter-plus' )+' '+pro_text() , disabled: is_pro_active });
			templates.push({ value: 4 , label: __( 'Template-4', 'filter-plus' )+' '+pro_text() , disabled: is_pro_active });
			
			return templates;
		}

        return (
			<>
				<InspectorControls>
					<PanelBody title={ __( 'Settings', 'filter-plus' ) }>
						<SelectControl
							label={ __( 'Select Template', 'filter-plus' ) }
							value={ template }
							options={ getTemplates() }
							onChange={ onChangeTemplate }
						/>
						<SelectControl
							label={ __( 'Select Filter Type', 'filter-plus' ) }
							value={ filter_type }
							options={ [
								{ value: 'post' , label: __( 'Post', 'filter-plus' ) , disabled: is_pro_active },
								{ value: 'custom_post' , label: __( 'Custom Post', 'filter-plus' ) , disabled: is_pro_active }
								] }
							onChange={ onChangeFilterType }
						/>
						{
							filter_type == "custom_post" && 
								<Fragment>
									<SelectControl
										label={ __( 'Select Custom Post Type', 'filter-plus' ) }
										value={ custom_post }
										options={ filterPlus?.custom_post_type }
										onChange={ onChangeCustomPost }
									/>
								</Fragment>
						}
						
						<ToggleControl
							label={ __( 'Display Categories', 'filter-plus' ) }
							checked={ show_categories }
							onChange={ onChangeShowCategory }
						/>
						{
							show_categories &&
							<Fragment>
								<TextControl
									multiple
									label={ __( 'Category Label', 'filter-plus' ) }
									help={ __( 'Place Category Label', 'filter-plus' ) }
									value={ category_label }
									onChange={ onChangeCategoryLabel }
								/>
								<SelectControl
									multiple
									label={ __( 'Category List', 'filter-plus' ) }
									value={ categories }
									options={filterPlus?.wp_cats}
									onChange={ onChangeCatList }
								/>
							</Fragment>
						}

						<ToggleControl
							label={ __( 'Display Tags', 'filter-plus' ) }
							checked={ show_tags }
							onChange={ onChangeDisplayTags }
						/>

						{
						show_tags &&
							<Fragment>
								<TextControl
									multiple
									label={ __( 'Tag Label', 'filter-plus' ) }
									value={ tag_label }
									onChange={ onChangeTagLabel }
								/>
								<SelectControl
									multiple
									label={ __( 'Tags', 'filter-plus' ) }
									value={ tags }
									options={filterPlus?.post_tag}
									onChange={ onChangeTagist }
								/>
							</Fragment>
						}
						<ToggleControl
							label={ __( 'Display Authors', 'filter-plus' ) }
							checked={ author }
							onChange={ onChangeShowAuthor }
						/>
						{
							author &&
							<Fragment>
								<TextControl
									multiple
									label={ __( 'Author Label', 'filter-plus' ) }
									help={ __( 'Place Author Label', 'filter-plus' ) }
									value={ author_label }
									onChange={ onChangeAuthorLabel }
								/>
								<SelectControl
									multiple
									label={ __( 'Author List', 'filter-plus' ) }
									value={ author_list }
									options={filterPlus?.author_list}
									onChange={ onChangeAuthorList }
								/>
							</Fragment>
						}
						<ToggleControl
							label={ __( 'Display Custom Field', 'filter-plus' ) }
							checked={ custom_field }
							onChange={ onChangeCustomField }
						/>
						{
							custom_field &&
							<Fragment>
								<TextControl
									multiple
									label={ __( 'Custom Field Label', 'filter-plus' ) }
									help={ __( 'Custom Field Label', 'filter-plus' ) }
									value={ custom_field_label }
									onChange={ onChangeCustomFieldLabel }
								/>
								<TextControl
									multiple
									label={ __( 'Custom Field Name', 'filter-plus' ) }
									help={ __( 'Enter Exact Custom Field Name', 'filter-plus' ) }
									value={ custom_field_list }
									onChange={ onChangeCustomFieldList }
								/>
							</Fragment>
						}
						<ToggleControl
							label={ __( 'Display Categories in Filter Result', 'filter-plus' ) }
							checked={ post_categories }
							onChange={ onChangePostCat }
						/>
						<ToggleControl
							label={ __( 'Display Tags in Filter Result', 'filter-plus' ) }
							checked={ post_tags }
							onChange={ onChangePostTag }
						/>
						<ToggleControl
							label={ __( 'Display Author in Filter Result', 'filter-plus' ) }
							checked={ post_author }
							onChange={ onChangePostAuthor }
						/>
					</PanelBody>
				</InspectorControls>

				<div { ...blockProps }>
					{ __('Customize the Wordpress filtering options from the block settings', 'filter-plus') }
				</div>
			</>
		);
	},

	save( { attributes } ) {
		const {
			filter_type,
			custom_post,
			template,
			show_categories,
			category_label,
			categories,
            show_tags,
			tag_label,
            tags,
            author,
            author_label,
            author_list,
            custom_field,
            custom_field_label,
            meta_condition,
            custom_field_list,
			post_categories,
			post_tags,
			post_author
		} = attributes;

		const blockProps = useBlockProps.save();

		return (
			<div {...blockProps}>
			</div>
		);
	},

} );
