import { registerBlockType } from '@wordpress/blocks';
import {
	InspectorControls,
	useBlockProps,
} from '@wordpress/block-editor';

import {
	TextControl,
	ToggleControl,
	SelectControl,
	PanelBody,
} from '@wordpress/components';

const { __ } = wp.i18n;

registerBlockType('filter-plus/woo-filter', {
    title: __( 'WooCommerce Product Filter', 'filter-plus' ),
    icon: 'image-filter',
    category: 'text',
	attributes: {
		category_label: {
			type: 'string',
		},
		color_label: {
			type: 'string',
		},
		size_label: {
			type: 'string',
		},
		tag_label: {
			type: 'string',
		},
		review_label: {
			type: 'string',
		},
		price_range_label: {
			type: 'string',
		},
		attribute_label: {
			type: 'string',
		},
		on_sale_label: {
			type: 'string',
		},
		stock_label: {
			type: 'string',
		},
		colors: {
			type: 'boolean',
		},
		size: {
			type: 'boolean',
		},
		template: {
			type: 'string',
			default: '1' ,
		},
		categories: {
			type: 'array',
			default: [] ,
		},
		show_tags: {
			type: 'boolean',
		},
		stock: {
			type: 'boolean',
		},
		on_sale: {
			type: 'boolean',
		},
		tags: {
			type: 'array',
			default: [] ,
		},
		show_attributes: {
			type: 'boolean',
		},
		attribute_list: {
			type: 'array',
			default: [] ,
		},
		show_reviews: {
			type: 'boolean',
		},
		show_price_range: {
			type: 'boolean',
		},
		sorting: {
			type: 'boolean',
		},
		product_tags: {
			type: 'boolean',
		},
		product_categories: {
			type: 'boolean',
		},
	},

	edit( { attributes, setAttributes } ) {
        const blockProps = useBlockProps();

        const {
			category_label,
			color_label,
			size_label,
			tag_label,
			review_label,
			price_range_label,
			attribute_label,
			on_sale_label,
			stock_label,
            colors,
            size,
            template,
            categories,
            show_tags,
            tags,
			show_attributes,
			attribute_list,
			show_reviews,
			show_price_range,
			on_sale,
			stock,
			sorting,
			product_tags,
			product_categories
        } = attributes;

		function onChangeCategoryLabel( newValue ) {
            setAttributes( { category_label: newValue } );
        }
		function onChangeColorLabel( newValue ) {
            setAttributes( { color_label: newValue } );
        }
		function onChangeSizeLabel( newValue ) {
            setAttributes( { size_label: newValue } );
        }
		function onChangeTagLabel( newValue ) {
            setAttributes( { tag_label: newValue } );
        }
		function onChangeReviewLabel( newValue ) {
            setAttributes( { review_label: newValue } );
        }
		function onChangePriceRangeLabel( newValue ) {
            setAttributes( { price_range_label: newValue } );
        }
		function onChangeAttributeLabel( newValue ) {
            setAttributes( { attribute_label: newValue } );
        }
		function onChangeOnSaleLabel( newValue ) {
            setAttributes( { on_sale_label: newValue } );
        }
		function onChangeStockLabel( newValue ) {
            setAttributes( { stock_label: newValue } );
        }
        function onChangeDisplayColor( newValue ) {
            setAttributes( { colors: newValue } );
        }
		function onChangeDisplaySize( newValue ) {
            setAttributes( { size: newValue } );
        }
        function onChangeCatList( newValue ) {
            setAttributes( { categories: newValue } );
        }
        function onChangeTemplate( newValue ) {
            setAttributes( { template: newValue } );
        }
		function onChangeDisplayTags( newValue ) {
            setAttributes( { show_tags: newValue } );
        }
        function onChangeTagist( newValue ) {
            setAttributes( { tags: newValue } );
        }
		function onChangeDisplayAttr( newValue ) {
            setAttributes( { show_attributes: newValue } );
        }
        function onChangeAttr( newValue ) {
            setAttributes( { attribute_list: newValue } );
        }
		function onChangePriceRangse( newValue ) {
            setAttributes( { show_price_range: newValue } );
        }
		function onChangeStockStatus( newValue ) {
            setAttributes( { stock: newValue } );
        }
		function onChangeSales( newValue ) {
            setAttributes( { on_sale: newValue } );
        }
		function onChangeShowReviews( newValue ) {
            setAttributes( { show_reviews: newValue } );
        }
		function onChangeSorting( newValue ) {
            setAttributes( { sorting: newValue } );
        }
		function onChangeProductTag( newValue ) {
            setAttributes( { product_tags: newValue } );
        }
		function onChangeProductCat( newValue ) {
            setAttributes( { product_categories: newValue } );
        }
		
		function pro_text() {
			return filterPlus.is_pro_active == 1 ? __( '(Pro)', 'filter-plus' ) : '';
		}
		function getTemplates() {
			let is_pro_active = filterPlus.is_pro_active == 0 ? 0 : 1;
			let templates = [ { value: 2 , label: __( 'Template-1', 'filter-plus' )+' '+pro_text() , disabled: is_pro_active } ];
			templates.push({ value: 2 , label: __( 'Template-2', 'filter-plus' )+' '+pro_text() , disabled: is_pro_active });
			templates.push({ value: 3 , label: __( 'Template-3', 'filter-plus' )+' '+pro_text() , disabled: is_pro_active });
			templates.push({ value: 4 , label: __( 'Template-4', 'filter-plus' )+' '+pro_text() , disabled: is_pro_active });
			
			return templates;
		}

        return (
			<>
				<InspectorControls>
					<PanelBody title={ __( 'Settings', 'filter-plus' ) }>
						<SelectControl
							label={ __( 'Select Template', 'filter-plus' ) }
							value={ template }
							options={ getTemplates() }
							onChange={ onChangeTemplate }
						/>
						<TextControl
							multiple
							label={ __( 'Category Label', 'filter-plus' ) }
							help={ __( 'Place Category Label', 'filter-plus' ) }
							value={ category_label }
							onChange={ onChangeCategoryLabel }
						/>
						<SelectControl
							multiple
							label={ __( 'Category List', 'filter-plus' ) }
							value={ categories }
							options={filterPlus?.woo_categories}
							onChange={ onChangeCatList }
						/>
						<ToggleControl
							label={ __( 'Display Colors', 'filter-plus' ) }
							checked={ colors }
							onChange={ onChangeDisplayColor }
						/>
						{
							colors &&
							<TextControl
								multiple
								label={ __( 'Color Label', 'filter-plus' ) }
								value={ color_label }
								onChange={ onChangeColorLabel }
							/>

						}
						
						<ToggleControl
							label={ __( 'Display Size', 'filter-plus' ) }
							checked={ size }
							onChange={ onChangeDisplaySize }
						/>
						{
							size &&
							<TextControl
								multiple
								label={ __( 'Size Label', 'filter-plus' ) }
								value={ size_label }
								onChange={ onChangeSizeLabel }
							/>
						}
						<ToggleControl
							label={ __( 'Display Tags', 'filter-plus' ) }
							checked={ show_tags }
							onChange={ onChangeDisplayTags }
						/>

						{
						show_tags &&
							<Fragment>
								<TextControl
									multiple
									label={ __( 'Tag Label', 'filter-plus' ) }
									value={ tag_label }
									onChange={ onChangeTagLabel }
								/>
								<SelectControl
									multiple
									label={ __( 'Tags', 'filter-plus' ) }
									value={ tags }
									options={filterPlus?.tags}
									onChange={ onChangeTagist }
								/>
							</Fragment>
						}

						<ToggleControl
							label={ __( 'Display Attributes', 'filter-plus' ) }
							checked={ show_attributes }
							onChange={ onChangeDisplayAttr }
						/>
						{
						show_attributes &&
							<Fragment>
								<TextControl
									multiple
									label={ __( 'Attribute Label', 'filter-plus' ) }
									value={ attribute_label }
									onChange={ onChangeAttributeLabel }
								/>
								<SelectControl
									multiple
									label={ __( 'Attributes', 'filter-plus' ) }
									value={ attribute_list }
									options={filterPlus?.attributes}
									onChange={ onChangeAttr }
								/>
							</Fragment>
						}

						<ToggleControl
							label={ __( 'Display Reviews', 'filter-plus' ) }
							checked={ show_reviews }
							onChange={ onChangeShowReviews }
						/>
						{
							show_reviews &&
							<TextControl
								multiple
								label={ __( 'Review Label', 'filter-plus' ) }
								value={ review_label }
								onChange={ onChangeReviewLabel }
							/>
						}
						<ToggleControl
							label={ __( 'Display Price Range', 'filter-plus' ) }
							checked={ show_price_range }
							onChange={ onChangePriceRangse }
						/>
						{
							show_price_range &&
							<TextControl
								multiple
								label={ __( 'Price Range Label', 'filter-plus' ) }
								value={ price_range_label }
								onChange={ onChangePriceRangeLabel }
							/>
						}
						<ToggleControl
							label={ __( 'Filter By Stock', 'filter-plus' ) }
							checked={ stock }
							onChange={ onChangeStockStatus }
						/>
						{
						stock &&
							<TextControl
								multiple
								label={ __( 'Stock Label', 'filter-plus' ) }
								value={ stock_label }
								onChange={ onChangeStockLabel }
							/>
						}
						<ToggleControl
							label={ __( 'Sales', 'filter-plus' ) }
							checked={ on_sale }
							onChange={ onChangeSales }
						/>
						{
							on_sale &&
							<TextControl
								multiple
								label={ __( 'Sale Label', 'filter-plus' ) }
								value={ on_sale_label }
								onChange={ onChangeOnSaleLabel }
							/>
						}

						<ToggleControl
							label={ __( 'Display Sorting', 'filter-plus' ) }
							checked={ sorting }
							onChange={ onChangeSorting }
						/>
						<ToggleControl
							label={ __( 'Display Tags in Filter Result', 'filter-plus' ) }
							checked={ product_tags }
							onChange={ onChangeProductTag }
						/>
						<ToggleControl
							label={ __( 'Display Categories in Filter Result', 'filter-plus' ) }
							checked={ product_categories }
							onChange={ onChangeProductCat }
						/>
					</PanelBody>
				</InspectorControls>

				<div { ...blockProps }>
					{ __('Customize the Woocommerce filtering options from the block settings', 'filter-plus') }
				</div>
			</>
		);
	},

	save( { attributes } ) {
		const {
			category_label,
			color_label,
			size_label,
			tag_label,
			review_label,
			price_range_label,
			attribute_label,
			on_sale_label,
			stock_label,
            colors,
            size,
            template,
            categories,
            show_tags,
            tags,
			show_attributes,
			attribute_list,
			show_reviews,
			show_price_range,
			stock,
			on_sale,
			sorting,
			product_tags,
			product_categories
		} = attributes;

		const blockProps = useBlockProps.save();

		return (
			<div {...blockProps}>
			</div>
		);
	},

} );
